/* Gloabl styles */
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Arial", sans-serif;
}

body {
	background-color: #0b0f1a;
	color: #fff;
	font-size: 16px;
	line-height: 1.6;
}

section {
	padding: 70px 0;
	color: #fff;
	background-color: #10131c;
}

.secondary-background {
	background-color: #1b1e2b;
}

.alt-background {
	background-color: #0d111e;
}

.container {
	max-width: 1200px;
	margin: 0 auto;
	padding: 0 20px;
}

.btn {
	padding: 10px 20px;
	border-radius: 5px;
	font-weight: bold;
	text-transform: uppercase;
	text-decoration: none;
	transition: background-color 0.3s ease;
}

.btn-primary {
	background-color: #0085ff;
	color: #fff;
}

.btn-secondary {
	background-color: transparent;
	color: #fff;
	border: 2px solid #fff;
}

.btn-cta {
	background-color: #0085ff;
	color: #fff;
	padding: 15px 30px;
	border-radius: 50px;
}
